.tabContainer {
  display: flex;
  /* 4px is the border width so everything lines up */
  margin-left: calc(var(--sidebarWidth) + var(--leftMargin) - 4px);
  margin-right: calc(var(--rightSidebar) - var(--leftMargin) + 4px);
  margin-top: 10px;
  height: 25%;
  z-index: 100;
}

.tab {
  height: 100%;
  letter-spacing: 0px;
  border-bottom: 5px solid black;
  background-color: #9a9a9a;
  width: 20%;
  text-align: center;
  border-radius: 10px 10px 0 0;
  margin-left: 5px;
  margin-right: 5px;
}

.tabText {
  height: 100%;
  text-align: center;
  font-size: 18px;
  color: #000000;
  font-family: Georgia;
}

.tabCloseButton {
  height: 100%;
  font-size: 18px;
  color: #000000;
  margin-left: 5px;
}

.tab:hover button:last-child {
  visibility: visible;
}

.tab button:last-of-type {
  visibility: hidden;
}

.openTab {
  background-color: white;
  border-bottom: 18px solid white;
}

.tab:first-of-type {
  margin-left: 0px;
}

.tab:last-of-type {
  margin-right: 0px;
}

@media (max-width: 768px) {
  .tabContainer {
    width: calc(100% - 50px);
    margin-left: 50px;
    margin-right: 0;
  }
  .tab {
    border-radius: 5px;
    border-bottom: 0;
    width: fit-content;
    padding-left: 5px;
    padding-right: 5px;
  }
  .tabText {
    font-size: 18px;
  }
  .tabCloseButton {
    display: none;
  }
  .openTab {
    border-bottom: 0;
  }
}
